<template>
	<div class="userBody">
<!-- 		<body  ontouchstart=""> -->
		<!--  -->
			<div class="topBox">
				<a href="javascript:history.go(-1)" class="back"></a>
				<div class="kazari"></div>
				<div class="user-pic">
					<div class="portrait">
							<img id="pic" ref="pic" :src="avatar" height="86" width="86">
						<input id="newPicBtn" type="file" @change="fileUpload" accept="image/*" ref="photoUpload">
					</div>
					<p class="user-name"></p>
				</div>
				<div class="user-mes">
					<span>100</span>
					<span>23</span>
				</div>
			</div>
			<cropper :imgsrc="cropperImg" v-if="isEdit" @confirm="confirmAvatar" @close="closeCropper"></cropper>
			<!--  -->
 			<ul class="first-list">
				<li class="title"></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="yue"></span><p></p></div></router-link></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="tixian"></span><p></p></div></router-link></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="more"></span><p></p></div></router-link></li>
			</ul>
			<ul class="first-list">
				<li class="title"></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="rent-order"></span><p></p></div></router-link></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="account-manage"></span><p></p></div></router-link></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="lease-record"></span><p></p></div></router-link></li>
			</ul>
			<ul class="first-list">
				<li class="title"></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="point-order"></span><p></p></div></router-link></li>
			</ul>
			<ul class="first-list">
				<li class="title"></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="dailian-publish"></span><p></p></div></router-link></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="dailian-accept"></span><p></p></div></router-link></li>
			</ul>
			<ul class="first-list testt">
				<li class="title"></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="point-canyu"></span><p></p></div></router-link></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="point-lock"></span><p></p></div></router-link></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="bang-det"></span><p></p></div></router-link></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="shiming"></span><p></p></div></router-link></li>
				<li><router-link to="/UserList?type=" tag="a"><div><span class="complain"></span><p></p></div></router-link></li>
				<!-- <?php if($info['is_distributor']==1){  ?> -->
				<li><router-link to="/UserList?type=" tag="a"><div><span class="manage"></span><p></p></div></router-link></li>
				<!-- <?php } ?> -->
			</ul>
			<div class="bottom_area">
				<a @click="signOut"><button type="button" id="sign_out"></button></a>
			</div>
	</div>
</template>
<script>
import tipShow from "@/assets/js/tips.js"
import cropper from "./cropper.vue";
import { Toast  } from 'vant';
	export default{
		data(){
			return {
				cropperImg:"",
				isEdit:false,
				avatar:""			
			}
		},
		methods:{
			signOut (){
				this.$store.commit("signOut",false);
				tipShow("")
				this.$router.back()
			},
			fileUpload:function(e) {
					var thisBox = this.$refs['photoUpload'];
					var files = e.target.files;
					var file = files[0];
					var that=this;
					if (!/image.(png|jpg|jpeg|svg\+xml|gif)/.test(file.type)) { 
						tipShow(''); 
						return; 
					}
					const toast = Toast.loading({
					  duration: 0,       //  toast
					  forbidClick: true, // 
					  loadingType: 'spinner',
					  mask:true,
					  message: ''
					});
					var reader = new FileReader();
					reader.onload=(event)=>{
						var str = event.target.result;
						var img = new Image();
						img.onload =()=>{
							this.cropperImg=str;
							this.isEdit=true;
							Toast.clear();
						} 
						img.src = str;       	
					}
				reader.readAsDataURL(file);
			},
			closeCropper(){
				this.isEdit=false;
			},
			confirmAvatar(src){
				this.avatar=src;
				localStorage.setItem("kzg-avatar",src);
				this.$toast('');
				this.isEdit=false;
			}
		},
		mounted:function() {
			this.avatar=localStorage.getItem("kzg-avatar")||require("@/assets/img/sony.jpg")
		},
		components:{
			cropper
		}
	}
</script>
<style scoped>
	@import url("../../assets/css/user.css")
</style>


